import React from "react"
import {Card, Table } from 'react-bootstrap';

import { useFinance } from "./FinanceContext";
import { PieChart } from "@mui/x-charts";
import { formatCurrency } from "./FormattingHelpers";

const ExpenseAnalysis = (props: {dateIndex: number}) => {
    
    const dateIndex = props.dateIndex;
    const {dataForDates, getTotalExpense, getTotalIncome, getTotalDebt, getTotalSaved } = useFinance();
    const expenses = dataForDates[dateIndex].expenses;
    const debts = dataForDates[dateIndex].debts;
    const savingsPerMonth = getTotalSaved(dateIndex);

    let expenseData = expenses.filter(expense => expense.cost > 0).map(expense => ({
        label: expense.name,
        value: expense.cost,
    }));

    let debtData = debts.filter(debt => debt.monthlyRepayment > 0).map(debt => ({
        label: debt.name,
        value: debt.monthlyRepayment,
    }));

    let data = expenseData.concat(debtData)

    const remaining = getTotalIncome(dateIndex) - getTotalExpense(dateIndex) - getTotalDebt(dateIndex) - savingsPerMonth;

    if(savingsPerMonth > 0)
    {
      data = [...data, {label: "Savings", value: savingsPerMonth}]
    }

    if(remaining > 0)
    {
      data = [...data, {label: "Remaining", value: remaining}]
    }

    return (
      <Card>
        <Card.Header>
          <Card.Title>
            Expense Breakdown
          </Card.Title>
        </Card.Header>
        <Card.Body>
        <Table striped bordered hover className="table text-center align-middle">
            <thead>
                <tr>
                    <th>Total Income</th>
                    <th>Monthly Saving Contribution</th>
                    <th>Total Monthly Expense</th>
                    <th>Total Monthly Debt Repayment</th>
                    <th>Remaining</th>
                </tr>
            </thead>
            <tbody>
              <tr>
                <td>{formatCurrency(getTotalIncome(dateIndex))}</td>
                <td>{formatCurrency(savingsPerMonth)}</td>
                <td>{formatCurrency(getTotalExpense(dateIndex))}</td>
                <td>{formatCurrency(getTotalDebt(dateIndex))}</td>
                <td>{formatCurrency(remaining)}</td>
              </tr>
            </tbody>
          </Table>
          <PieChart 
            width={600} 
            height={250} 
            series={[
              {
                data,
                highlightScope: { faded: 'global', highlighted: 'item' },
                faded: { innerRadius: 30, additionalRadius: -30, color: 'gray' },
                valueFormatter: (v) => (v === null ? '' : formatCurrency(v.value))
              },
            ]}
            />

        </Card.Body>
      </Card>
    );
  };
  
  export default ExpenseAnalysis;