import { ChangeEvent, Component } from "react";
import { Button, Card, Form, Table } from "react-bootstrap";
import { MoneyStream } from "./FinanceContext";

export interface MoneyStreamsProps
{
    name: string;

    dateIndex: number;
    streams: MoneyStream[];
    updateStream: (dateIndex: number, index: number, updatedExpense: MoneyStream) => void;
    addStream: (dateIndex: number) => void;
    deleteStream: (dateIndex: number, index: number) => void;
};

class MoneyStreams extends Component<MoneyStreamsProps>
{
    dateIndex: number;
    constructor(props: MoneyStreamsProps)
    {
        super(props)
        this.dateIndex = props.dateIndex;
    }
    
    renderStream = (expense: { name: string; cost: number; }, index: number) => {
        const { updateStream, deleteStream } = this.props;

        return (
            <tr key={index}>
                <td>
                    <Form.Control
                        type='text'
                        onChange={(e: { target: { value: string; }; }) => updateStream(this.dateIndex, index, { ...expense, name: e.target.value })}
                        value={expense.name ? expense.name : ""}
                        className="mr-2" />
                </td>
                <td>
                    <Form.Control
                        type='number'
                        onChange={(e: ChangeEvent<HTMLInputElement>) => 
                            updateStream(this.dateIndex, index, { ...expense, cost: parseFloat(e.target.value) })
                        }
                        value={expense.cost ? expense.cost : 0}
                        className="mr-2" />
                </td>
                <td>
                    <Form.Control
                        type='number'
                        onChange={(e: ChangeEvent<HTMLInputElement>) => 
                            updateStream(this.dateIndex, index, { ...expense, cost: parseFloat(e.target.value) })
                        }
                        value={(expense.cost * 12).toFixed(2)}
                        className="mr-2" 
                        readOnly/>
                </td>
                <td>
                    <Button variant="danger" onClick={() => deleteStream(this.dateIndex, index)}>X</Button>
                </td>
            </tr>
        );
    };

    render(){
        const { name, streams, addStream } = this.props;

        return (
            <>
                <Card.Header>
                    <Card.Title>
                        {name}s
                    </Card.Title>
                </Card.Header>
                <Card.Body>
                    <Table striped bordered hover className="table text-center align-middle">
                        <thead>
                            <tr>
                                <th>Item</th>
                                <th>Monthly </th>
                                <th>Annual </th>
                            </tr>
                        </thead>
                        <tbody>
                            {streams.map((stream, index: number) => this.renderStream(stream, index))}
                        </tbody>
                    </Table>
                    <Button variant="primary" onClick={() => addStream(this.dateIndex)}>
                        Add {name}
                    </Button>
                </Card.Body>
            </>
        );
    }
};



export default MoneyStreams