import { Button, Card, Col, Form, InputGroup, Row, Table } from "react-bootstrap";
import MoneyStreams, { MoneyStreamsProps } from "./MoneyStreams";
import { ChangeEvent, Component } from "react";
import { Debt, MoneyStream } from "./FinanceContext";

export interface DebtStreamsProps
{
    name: string;
    dateIndex: number;

    debts: Debt[];
    updateDebt: (dateIndex: number, index: number, updatedDebt: Debt) => void;
    addDebt: (dateIndex: number) => void;
    deleteDebt: (dateIndex: number, index: number) => void;
};

class DebtStreams extends Component<DebtStreamsProps>
{
    dateIndex: number
    constructor(props: DebtStreamsProps)
    {
        super(props)
        this.dateIndex = props.dateIndex;
    }

    renderDebt = (debt: Debt, index: number) => {
        const { updateDebt, deleteDebt } = this.props;

        return (
            <tr key={index}>
                <td>
                    <Form.Control
                        type='text'
                        onChange={(e: { target: { value: string; }; }) => updateDebt(this.dateIndex, index, { ...debt, name: e.target.value })}
                        value={debt.name ? debt.name : ""}
                        className="mr-2" />
                </td>
                <td>
                    <Form.Control
                        type='number'
                        onChange={(e: ChangeEvent<HTMLInputElement>) => 
                            updateDebt(this.dateIndex,index, { ...debt, minimumMonthlyRepayment: parseFloat(e.target.value) })
                        }
                        value={debt.minimumMonthlyRepayment ? debt.minimumMonthlyRepayment : 0}
                        className="mr-2" />
                </td>
                <td>
                    <Form.Control
                        type='number'
                        onChange={(e: ChangeEvent<HTMLInputElement>) => 
                            updateDebt(this.dateIndex,index, { ...debt, monthlyRepayment: parseFloat(e.target.value) })
                        }
                        value={debt.monthlyRepayment ? debt.monthlyRepayment : 0}
                        className="mr-2" />
                </td>
                <td>
                    <Form.Control
                        type='number'
                        onChange={(e: ChangeEvent<HTMLInputElement>) => 
                            updateDebt(this.dateIndex,index, { ...debt, total: parseFloat(e.target.value) })
                        }
                        value={debt.total ? debt.total : 0}
                        className="mr-2" />
                </td>
                <td>
                    <Form.Control
                        type='number'
                        onChange={(e: ChangeEvent<HTMLInputElement>) => 
                            updateDebt(this.dateIndex,index, { ...debt, interest: parseFloat(e.target.value) })
                        }
                        value={debt.interest ? debt.interest : 0}
                        className="mr-2" />
                </td>
                <td>
                    <Button variant="danger" onClick={() => deleteDebt(this.dateIndex, index)}>X</Button>
                </td>
            </tr>
        );
    };

    render(){
        const { name, debts, addDebt } = this.props;

        return (
            <>
                <Card.Header>
                    <Card.Title>
                        {name}s
                    </Card.Title>
                </Card.Header>
                <Card.Body>
                    <Table striped bordered hover className="table text-center align-middle">
                        <thead>
                            <tr>
                                <th>Item</th>
                                <th>Minimum Monthly Repayment </th>
                                <th>Monthly Repayment </th>
                                <th>Total to Repay </th>
                                <th>Annual Interest %</th>
                            </tr>
                        </thead>
                        <tbody>
                            {debts.map((debt, index: number) => this.renderDebt(debt, index))}
                        </tbody>
                    </Table>
                    <Button variant="primary" onClick={() => addDebt(this.dateIndex)}> Add {name} </Button>
                </Card.Body>
            </>
        );
    }
}

export default DebtStreams